<h1 id="用户组" tabindex="-1"><a class="header-anchor" href="#用户组" aria-hidden="true">#</a> 用户组</h1>
<p>用户组的使用就非常简单了，基本的增删改查。</p>
<h2 id="获取实例" tabindex="-1"><a class="header-anchor" href="#获取实例" aria-hidden="true">#</a> 获取实例</h2>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token php language-php"><span class="token delimiter important">&lt;?php</span>
<span class="token keyword">use</span> <span class="token package">EasyWeChat<span class="token punctuation">\</span>Foundation<span class="token punctuation">\</span>Application</span><span class="token punctuation">;</span>

<span class="token comment">// ...</span>

<span class="token variable">$app</span> <span class="token operator">=</span> <span class="token keyword">new</span> <span class="token class-name">Application</span><span class="token punctuation">(</span><span class="token variable">$options</span><span class="token punctuation">)</span><span class="token punctuation">;</span>

<span class="token variable">$group</span> <span class="token operator">=</span> <span class="token variable">$app</span><span class="token operator">-></span><span class="token property">user_group</span><span class="token punctuation">;</span> <span class="token comment">// $user['user_group']</span>
</span></code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br></div></div><h2 id="api" tabindex="-1"><a class="header-anchor" href="#api" aria-hidden="true">#</a> API</h2>
<h3 id="获取所有分组" tabindex="-1"><a class="header-anchor" href="#获取所有分组" aria-hidden="true">#</a> 获取所有分组</h3>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$group</span><span class="token operator">-></span><span class="token function">lists</span><span class="token punctuation">(</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><p>example:</p>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$groups</span> <span class="token operator">=</span> <span class="token variable">$group</span><span class="token operator">-></span><span class="token function">lists</span><span class="token punctuation">(</span><span class="token punctuation">)</span><span class="token punctuation">;</span>

<span class="token comment">// {</span>
<span class="token comment">//     "groups": [</span>
<span class="token comment">//         {</span>
<span class="token comment">//             "id": 0,</span>
<span class="token comment">//             "name": "未分组",</span>
<span class="token comment">//             "count": 72596</span>
<span class="token comment">//         },</span>
<span class="token comment">//         {</span>
<span class="token comment">//             "id": 1,</span>
<span class="token comment">//             "name": "黑名单",</span>
<span class="token comment">//             "count": 36</span>
<span class="token comment">//         },</span>
<span class="token comment">//         ...</span>
<span class="token comment">//     ]</span>
<span class="token comment">// }</span>

<span class="token function">var_dump</span><span class="token punctuation">(</span><span class="token variable">$groups</span><span class="token operator">-></span><span class="token property">groups</span><span class="token punctuation">[</span><span class="token number">0</span><span class="token punctuation">]</span><span class="token punctuation">[</span><span class="token string single-quoted-string">'name'</span><span class="token punctuation">]</span><span class="token punctuation">)</span> <span class="token comment">// “未分组”</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br><span class="line-number">9</span><br><span class="line-number">10</span><br><span class="line-number">11</span><br><span class="line-number">12</span><br><span class="line-number">13</span><br><span class="line-number">14</span><br><span class="line-number">15</span><br><span class="line-number">16</span><br><span class="line-number">17</span><br><span class="line-number">18</span><br><span class="line-number">19</span><br></div></div><h3 id="创建分组" tabindex="-1"><a class="header-anchor" href="#创建分组" aria-hidden="true">#</a> 创建分组</h3>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$group</span><span class="token operator">-></span><span class="token function">create</span><span class="token punctuation">(</span><span class="token variable">$name</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><p>example:</p>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$group</span><span class="token operator">-></span><span class="token function">create</span><span class="token punctuation">(</span><span class="token variable">$name</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><h3 id="修改分组信息" tabindex="-1"><a class="header-anchor" href="#修改分组信息" aria-hidden="true">#</a> 修改分组信息</h3>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$group</span><span class="token operator">-></span><span class="token function">update</span><span class="token punctuation">(</span><span class="token variable">$groupId</span><span class="token punctuation">,</span> <span class="token variable">$name</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><p>example:</p>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$group</span><span class="token operator">-></span><span class="token function">update</span><span class="token punctuation">(</span><span class="token variable">$groupId</span><span class="token punctuation">,</span> <span class="token string double-quoted-string">"新的组名"</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><h3 id="删除分组" tabindex="-1"><a class="header-anchor" href="#删除分组" aria-hidden="true">#</a> 删除分组</h3>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$group</span><span class="token operator">-></span><span class="token function">delete</span><span class="token punctuation">(</span><span class="token variable">$groupId</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><p>example:</p>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$group</span><span class="token operator">-></span><span class="token function">delete</span><span class="token punctuation">(</span><span class="token variable">$groupId</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><h3 id="移动单个用户到指定分组" tabindex="-1"><a class="header-anchor" href="#移动单个用户到指定分组" aria-hidden="true">#</a> 移动单个用户到指定分组</h3>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$group</span><span class="token operator">-></span><span class="token function">moveUser</span><span class="token punctuation">(</span><span class="token variable">$openId</span><span class="token punctuation">,</span> <span class="token variable">$groupId</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><p>example:</p>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$group</span><span class="token operator">-></span><span class="token function">moveUser</span><span class="token punctuation">(</span><span class="token variable">$openId</span><span class="token punctuation">,</span> <span class="token variable">$groupId</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><h3 id="批量移动用户到指定分组" tabindex="-1"><a class="header-anchor" href="#批量移动用户到指定分组" aria-hidden="true">#</a> 批量移动用户到指定分组</h3>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$group</span><span class="token operator">-></span><span class="token function">moveUsers</span><span class="token punctuation">(</span><span class="token keyword type-hint">array</span> <span class="token variable">$openIds</span><span class="token punctuation">,</span> <span class="token variable">$groupId</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><p>example:</p>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$openIds</span> <span class="token operator">=</span> <span class="token punctuation">[</span><span class="token variable">$openId1</span><span class="token punctuation">,</span> <span class="token variable">$openId2</span><span class="token punctuation">,</span> <span class="token variable">$openId3</span> <span class="token operator">...</span><span class="token punctuation">]</span><span class="token punctuation">;</span>
<span class="token variable">$group</span><span class="token operator">-></span><span class="token function">moveUsers</span><span class="token punctuation">(</span><span class="token variable">$openIds</span><span class="token punctuation">,</span> <span class="token variable">$groupId</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br></div></div><p>关于用户管理请参考微信官方文档：http://mp.weixin.qq.com/wiki/ <code>用户管理</code> 章节。</p>
